$primary: #006444;

.hero {
  height: 0;
  min-height: 380px;
  max-height: 800px;
  background: url("/assets/images/cover-gauges.jpg") no-repeat center center, $primary;
  background-size: cover;
  text-align: center;
  position: relative;
}

.featured {
  @apply h-96 w-auto mx-auto;
  @apply absolute -bottom-20 left-0 right-0 md:right-auto md:left-[10%];
}